import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-template-message-empty-state.scss';
import '@servicenow/now-button';
import '@servicenow/now-illustration';
import '@servicenow/now-message';
import resizeBehavior from '@servicenow/behavior-resize';
import focusBehavior from '@servicenow/behavior-focus';
import {configAriaSchema} from '@servicenow/library-schemas';

const renderIllustration = (illustration) => {
	if (illustration) {
		return (
			<now-illustration
				className="now-template-message-empty-state-illustration"
				slot="media"
				size="auto"
				illustration={illustration}
			/>
		);
	}
};

const renderHeading = (heading) => {
	const {level = '3', label} = heading;
	const HeadingLevel = `h${level}`;
	return (
		<HeadingLevel className="now-template-message-empty-state-heading">
			{label}
		</HeadingLevel>
	);
};

const renderContent = (content) => {
	if (content) {
		return (
			<p className="now-template-message-empty-state-content">{content}</p>
		);
	}
};

const renderAction = (action, size, dispatch) => {
	return (
		<now-button
			className="now-template-message-empty-state-action"
			size={size === 'sm' ? 'sm' : 'md'}
			variant={action.variant}
			label={action.label}
			icon={action.icon}
			disabled={action.disabled}
			tooltip-content={action.tooltipContent}
			config-aria={action.configAria}
			on-click={() => {
				if (action.disabled) return;
				const type =
					action.clickActionType ||
					'NOW_TEMPLATE_MESSAGE_EMPTY_STATE#ACTION_CLICKED';
				dispatch(type, {action});
			}}
		/>
	);
};

const renderActions = (actions, size, dispatch) => {
	if (Array.isArray(actions) && actions.length) {
		return (
			<div slot="actions" className="now-template-message-empty-state-actions">
				{actions.map((action) => renderAction(action, size, dispatch))}
			</div>
		);
	}
};

const view = (state, {dispatch}) => {
	const {actions, alignment, content, heading, illustration} = state.properties;
	const {size} = state;
	return (
		<now-message
			class={{
				'now-template-message-empty-state': true,
				[`-${size}`]: size,
				[`-${alignment}`]: alignment
			}}
			alignment={alignment}>
			{renderIllustration(illustration)}
			<div slot="message" className="now-template-message-empty-state-messages">
				{renderHeading(heading)}
				{renderContent(content)}
			</div>
			{renderActions(actions, size, dispatch)}
		</now-message>
	);
};

/**
 * Component that lays out an illustration, heading, short description, and optional action buttons.
 *
 * ```jsx
 * <now-template-message-empty-state
 *   heading={{label: "No results found", level: "4"}}
 *   content="Please try your search again"
 *   illustration="no-search-results"
 *   actions={[{label: 'Go', variant: 'primary'}]}
 * />
 * ```
 *
 * @seismicElement now-template-message-empty-state
 * @summary Component that lays out an illustration, heading, short description, and optional action buttons.
 * @uib.label Empty state
 * @uib.icon template-message-empty-state
 * @uib.description Component that lays out an illustration, heading, short description, and optional action buttons.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties illustration, heading, content, actions, alignment
 * @uib.actions NOW_TEMPLATE_MESSAGE_EMPTY_STATE#ACTION_CLICKED
 */
createEnhancedElement('now-template-message-empty-state', {
	properties: {
		/**
		 * An array of objects that defines the buttons displayed.
		 * The keys for each object are properties for `now-button`. See
		 * `now-button` for more information on using these properties.
		 * - `label: string` required - Label describing the action.
		 * - `icon: string` - Icon displayed with the label.
		 * - `variant: string` - Button style variant.
		 * - `disabled: boolean` - Set to `true` to mute the button color and disable user click interactions.
		 * - `tooltipContent: string` - Text to display in the optional tooltip associated with the button.
		 * - `configAria: object` - Object with aria attributes to set on the action button.
		 * - `clickActionType: string = "NOW_TEMPLATE_MESSAGE_EMPTY_STATE#ACTION_CLICKED"` - Overrides the default action type dispatched when the user clicks the action
		 * @type {{ label: string, icon: string, variant: string, disabled: boolean, tooltipContent: string, configAria: object, clickActionType: string}[]}
		 * @uib.description Array of action buttons to display in the component
		 */
		actions: {
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						label: {type: 'string'},
						icon: {type: 'string'},
						variant: {type: 'string'},
						disabled: {type: 'boolean'},
						tooltipContent: {type: 'string'},
						configAria: configAriaSchema,
						clickActionType: {type: 'string'}
					}
				}
			}
		},
		/**
		 * Sets the alignment, either vertical-centered or horizontal.
		 * @type {('vertical-centered'|'horizontal')}
		 * @uib.description Sets the alignment
		 */
		alignment: {
			default: 'vertical-centered',
			schema: {
				type: 'string',
				enum: ['vertical-centered', 'horizontal']
			}
		},
		/**
		 * Secondary text to display in the empty state message.
		 * @type {string}
		 * @uib.description Secondary text to display in the empty state message
		 * @uib.defaultValue Please try your search again
		 */
		content: {
			schema: {type: 'string'}
		},
		/**
		 * An object with properties to configure the primary text for the empty state:
		 * - `label` - Text content for the heading.
		 * - `level = 3` - HTML heading element number used to render the heading.
		 * @type {{label: string, level: ('1'|'2'|'3'|'4'|'5'|'6')}}
		 * @uib.description Primary text to display in the empty state message
		 * @uib.defaultValue {"label": "No results found", "level": "3"}
		 */
		heading: {
			required: true,
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					level: {type: 'string', enum: ['1', '2', '3', '4', '5', '6']}
				}
			}
		},
		/**
		 * Name of the illustration to use.
		 * @type {('add-attachment'|'add-data'|'completed-tasks'|'error'|'first-time-user'|'interrupted'|'no-activities'|'no-data'|'no-search-results'|'offline'|'permissions'|'unconfigured')}
		 * @uib.description Name of the illustration to use
		 * @uib.defaultValue no-search-results
		 */
		illustration: {
			schema: {
				type: 'string',
				enum: [
					'add-attachment',
					'add-data',
					'completed-tasks',
					'error',
					'first-time-user',
					'interrupted',
					'no-activities',
					'no-data',
					'no-search-results',
					'offline',
					'permissions',
					'unconfigured'
				]
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks an action button. The payload will include a
		 * reference to the clicked action. The action type can be overridden
		 * by configuring `clickActionType`.
		 * @type {{action: Object}}
		 */
		'NOW_TEMPLATE_MESSAGE_EMPTY_STATE#ACTION_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					action: {
						type: 'object',
						properties: {
							label: {type: 'string'},
							icon: {type: 'string'},
							variant: {type: 'string'},
							disabled: {type: 'boolean'},
							clickActionType: {type: 'string'}
						}
					}
				}
			}
		}
	},
	initialState: {
		size: 'sm'
	},
	behaviors: [
		{behavior: focusBehavior},
		{
			behavior: resizeBehavior,
			options: {
				timer: {type: 'throttle', wait: 500},
				preventRender: true
			}
		}
	],
	actionHandlers: {
		['NOW_RESIZE#ELEMENT_RESIZED']: ({action, properties, updateState}) => {
			const {width, height} = action.payload;
			const {alignment} = properties;
			if (
				(alignment === 'vertical-centered' && width >= 500) ||
				(alignment === 'horizontal' && height >= 500)
			) {
				updateState({size: 'lg'});
			} else if (
				(alignment === 'vertical-centered' && width >= 250) ||
				(alignment === 'horizontal' && height >= 250)
			) {
				updateState({size: 'md'});
			} else {
				updateState({size: 'sm'});
			}
		}
	},
	styles,
	view
});
